

.busqueda {
  margin-top: 4%;
  width: 100%;
  display: flex;
  font-weight: bold;
  background-color: #da6517ce;
  padding: 0%;
  margin-bottom: 0%;
}

.busqueda form {
  flex: 1;
  display: flex;
}

.busqueda select {
  flex: 1;
  padding: 10px;
  border: none;
  background-color: #da6517ce;
  color: white;
  font-weight: bold;
}

.busqueda input[type="submit"] {
  width: 40px;
  padding: 10px;
  border: none;
  background-color: #da6517ce;
  background-image: url("../img/buscar.png");
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: center;
}

.busqueda a {
  padding: 10px;
  border-left: solid 1px;
  text-decoration: none;
  background-color: #da6517ce;
  color: #ffffff;
  font-size: 15px;
}

.busqueda a:hover,
.busqueda input[type="submit"]:hover,
.busqueda select:hover {
  cursor: pointer;
  background-color: #fd8637ce;
}

.menu {
  display: flex;
  width: 100%;
  height: 536px;
}

.menu a {
  width: 0px;
  flex-grow: 1;
  transition: 0.5s ease;
  display: flex;
  color: white;
  justify-content: center;
}

.menu a:hover {
  width: 300px;
}

.menu p {
  font-size: x-large;
  position: absolute;
  font-weight: bold;
  align-self: center;
  opacity: 0;
  transition: opacity 0.5s ease;
  z-index: 1;
}

.menu a:hover p {
  opacity: 1;
}

.menu img {
  width: 0px;
  flex-grow: 1;
  object-fit: cover;
  opacity: 0.8;
}

.menu a:hover img {
  opacity: 1;
  filter: brightness(70%);
}

.seleccionado {
  background-color: black;
  padding: 10px;
  color: #da6517ce;
  text-align: center;
}

.contenedor-imagen {
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.contenedor-imagen a {
  color: #1b3665;
  text-decoration: none;
  font-size: 20px;
  padding: 0%;
  margin: 0%;
}

.contenedor-imagen a:hover {
  color: #1b3665aa;
}

.contenedor-imagen p {
  color: #da6517ce;
  font-style: bold;
  font-size: 20px;
  padding: 0%;
  margin: 0%;
}

.informacionVehi {
  width: 100%;
  background-color: white;
  color: #1b3665;
  align-items: center;
  text-align: left;
  justify-content: center;
  display: flex;
}

.informacionVehi article {
  flex: 0.8;
  padding: 0px 40px;
}


.informacionVehi h1 {
  font-size: 20px;
  color: #da6517ce;
  text-transform: uppercase;
}

.informacionVehi strong {
  color: #fd8637ce;
}

.fotoVehiculo {
  width: 80%;
  height: 80%;
  object-fit: cover;
}

.btnAsignarChofer {
  background-color: #fd8637ce;
  color: #1b3665;
  border-radius: 5px;
  padding: 5px 15px;
  font-size: 16px;
  font-weight: bolder;
  border: 0;
  margin: 0px;
}

.btnAsignarChofer:hover {
  background-color: #fd8637a7;
  cursor: pointer;
}

#popupAsignarChofer,
#popupEliminarAsignacion {
  width: 60%;
  border: 5px solid #1b3665;
  border-radius: 20px;
}

#popupAsignarChofer::backdrop {
  background-color: rgba(0, 0, 0, 0.5);
}

#popupAsignarChofer p {
  color: #1b3665;
  margin: 20px auto;
  padding: 0;
  width: 75%;
}

.buscarCedula {
  display: flex;
  justify-content: center;
  align-items: center;
}

.cedulaBuscada {
  height: 25px;
  width: 40%;
  padding: 5px;
  border: 1px solid #1b3665;
  border-radius: 5px;
  margin: 10px;
}

#btnBuscarChofer {
  width: 35px;
  height: 35px;
  padding: 10px;
  background-color: black;
  background-image: url(../img/buscar.png);
  background-size: 80%;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 10px;
  border: none;
}

#btnBuscarChofer:hover {
  background-color: #1b3665;
  cursor: pointer;
}

.btnCerrar {
  background-color: red;
  color: white;
  border-radius: 5px;
  padding: 5px 15px;
  font-size: 16px;
  font-weight: bolder;
  position: absolute;
  top: 5px; /* Ajusta el valor según sea necesario para la posición vertical */
  right: 5px; /* Ajusta el valor según sea necesario para la posición horizontal */
}

.infoChoferPopup {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}

.infoChoferPopup article {
  padding: 10px;
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.infoChoferPopup article h1 {
  color: #da6517ce;
  text-transform: uppercase;
  font-size: larger;
}

.botones {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.botones button {
  margin: 0 10px;
}

#btnAsignar {
  background-color: rgb(0, 255, 0);
  color: #1b3665;
  border-radius: 5px;
  padding: 5px 15px;
  font-size: 16px;
  font-weight: bolder;
  border: 0;
}

#btnAsignar:hover {
  background-color: rgba(0, 255, 0, 0.6);
  cursor: pointer;
}

#btnCancelar {
  background-color: rgb(255, 89, 0);
  color: white;
  border-radius: 5px;
  padding: 5px 15px;
  font-size: 16px;
  font-weight: bolder;
  border: 0;
}

#btnCancelar:hover {
  background-color: rgba(255, 89, 0, 0.6);
  cursor: pointer;
}

.actividadVehiculo {
  width: 100%;
  height: auto;
  background-color: white;
  padding-top: 3%;
  padding-bottom: 3%;
  text-align: left;
}

.actividadVehiculo h1 {
  font-size: x-large;
  color: #da6517ce;
  margin: 2% 10%;
  margin-left: 13%;
  
}

.actividadVehiculo table {
  margin: 2% 10%;
  border: none;
  table-layout: fixed;
  width: 80%;
  border-collapse: collapse;
}

.actividadVehiculo thead {
  background-color: #1b3665;
  color: white;
}

.actividadVehiculo th {
  padding: 20px 30px;
  border: none;
  text-transform: uppercase;
}

.actividadVehiculo td {
  padding: 30px;
  border: none;
  border-bottom: solid 1px;
  text-align: center;
}

.actividadVehiculo tbody tr:hover {
  background-color: #b6bfcf;
}


.mant-container {
  width: 75%;
  height: auto;
  box-shadow: 3px 5px 10px black;
  border-radius: 5px;
  overflow: hidden;
  margin: auto;
}



.options {
  margin: 0%;
  padding: 0%;
  display: flex;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  list-style: none;
  background-color: rgb(218, 217, 217);
}

.options nav {
  align-items: end;
  margin-left: auto;
  align-content: center;
  padding: 0;
}

.options nav button {
  font-weight: bold;
  text-decoration: none;
  padding: 25px 15px;
  color: black;
  background-color: rgb(218, 217, 217);
  margin: 0%;
  border: none;
}

.options nav button:hover {
  background-color: rgb(155, 155, 155);
  transition: background-color 0.3s;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  color: white;
}

.option {
  font-weight: bold;
  flex-grow: 0.1;
  text-align: center;
  line-height: 60px;
  cursor: pointer;
  background-color: rgb(218, 217, 217);
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  transition: box-shadow 0.3s ease;
}

.option-active {
  font-weight: bold;
  border: solid 1px black;
  background: var(--bg-active);
  border-bottom: 1px solid rgba(7, 63 95, 0.4);
  text-align: center;
  line-height: 60px;
  border: solid 1px black;
  cursor: pointer;
  color: white;
  box-shadow: inset;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  /* Agrega una sombra al elemento seleccionado */
}

.contents {
  width: 100%;
  height: auto;
}

.content {
  width: 100%;
  height: auto;
  background: var(--bg-active);
  display: block;
}

/* .content-active {
  background: var(--bg-active);
  display: block;
} */

.contents h3 {
  text-align: center;
  font-size: large;
  padding-top: 1.5%;
  padding-bottom: 1%;
  border-bottom: solid 1px;
  margin: auto;
  width: 30%;
  color: white;
}

.contents p {
  text-align: center;
  font-size: large;
  padding-top: 5%;
  padding-bottom: 5%;
  margin: auto;
  width: 30%;
  color: white;
}

.table-container {
  margin: 2% 0;
  padding-bottom: 5%;
  overflow-y: auto;
  max-height: 400px;
  position: relative; /* Añade una posición relativa */
}

.table-container table {
  margin: 2% 10%;
  border: none;
  table-layout: fixed;
  width: 80%;
  border-collapse: collapse;
}

.table-container thead {
  background-color: black;
  color: white;
  position: sticky; /* Hace que el encabezado se mantenga fijo */
  top: 0; /* Fija el encabezado en la parte superior */
  z-index: 1; /* Asegura que el encabezado esté por encima del contenido */
}

.table-container th {
  padding: 20px 30px;
  border: none;
  text-transform: uppercase;
  text-align: center;

}

.table-container td {
  padding: 15px;

  border: none;
  border-bottom: solid 1px;
  text-align: center;
  color: white;
  font-size: 13px;
}



.table-container tbody tr:hover {
  background-color: #b6bfcf;
}


