.grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, auto);
}

#observacion {
  width: 70%;
  height: 50px;
  padding: 10px;
  border: 1px solid #1b3665;
  border-radius: 5px;
  margin: 10px;
}

.btnSiguiente {
  display: block;
  margin: 10px auto;
  background-image: url(../img/siguiente.png);
  background-size: 100%;
  background-repeat: no-repeat;
  background-color: #1b3665;
  border: none;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

#contenedorChofer{
  margin: 0%;
  margin-bottom: 15px;
}

.btnEliminarChofer {
  background-color: #fd8637ce;
  color: #1b3665;
  border-radius: 5px;
  padding: 5px 15px;
  margin: 10px 0;
  font-size: 16px;
  font-weight: bolder;
  border: 0;
}

.btnEliminarChofer:hover {
  background-color: #fd8637a7;
  cursor: pointer;
}

.btnSiguiente:hover {
  background-color: #1b3665a7;
  cursor: pointer;
}

.fotoChofer {
  width: 50%;
  object-fit: cover;
  margin-bottom: 10px;
}

.btnTerminarAccion{
  background-image: url("../img/activo.png");
  background-size: 100%;
  background-repeat: no-repeat;
  background-color: green;
  border: none;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin: 10px;
  cursor: pointer;
}

.estadoVehiculo {
  width: 15%;
  padding: 15%;
  height: 15%;
  background-size: cover;
  background-position: center;
  border-radius: 10px;
  font-size: x-large;
  margin-top: 0;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
  transition: 0.5s ease;
}

.estadoVehiculoActivo {
  background-color: rgb(3, 173, 3);
  background-image: url("../img/activo.png");
  margin: 0 auto;
}

.estadoVehiculoMantenimiento {
  background-color: gray;
  margin: 0 auto;
  background-image: url("../img/mantenimiento.png");
}

.estadoVehiculoRuta {
  background-color: #da6517ce;
  margin: 0 auto;
  background-image: url("../img/ruta.png");
}

.estadoVehiculoBaja {
  background-color: red;
  margin: 0 auto;
  background-image: url("../img/baja.png");
}